.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    background-image: url("../images/star.svg");
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;

    &:last-child {
      margin: 0;
    }
  }

  &--1 > .stars__star:first-child,
  &--2 > .stars__star:nth-of-type(-n+2),
  &--3 > .stars__star:nth-of-type(-n+3),
  &--4 > .stars__star:nth-of-type(-n+4),
  &--5 > .stars__star:nth-of-type(n) {
    background-image: url("../images/star-active.svg");
  }
}
